import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import MenuItem from '@mui/material/MenuItem';
import DataObjIcon from '@mui/icons-material/DataObject';
import { useState, MouseEvent } from 'react';
import Path from '../../../routes/paths';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { Npm } from 'assets/svgs';
import WhatsAppButton from '../../WhatsAppButton';
const pages = [
  { name: 'Projects', path: Path.Projects },
  { name: 'Page2', path: Path.Page2 },
  { name: 'Page3', path: Path.Page3 },
];

const NavigationBar = () => {
  const [anchorElNav, setAnchorElNav] = useState<null | HTMLElement>(null);

  const handleOpenNavMenu = (event: MouseEvent<HTMLElement>) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  return (
    <AppBar
      sx={{ backgroundColor: 'black' }}
      style={{
        fontFamily: 'Quicksand',
        backgroundImage: 'linear-gradient(to left, rgba(0,0,0,0),rgba(0, 140, 255, 0.3) 90%)',
      }}
      position="fixed"
    >
      <Triapoze>
        <WhatsAppButton />
      </Triapoze>
      <Toolbar style={{ padding: '0 0 0 50px', fontFamily: 'Quicksand' }} disableGutters>
        <DataObjIcon fontSize="large" sx={{ display: { xs: 'none', md: 'flex' }, mr: 0.5 }} />
        <Typography
          variant="h6"
          noWrap
          component="a"
          href="/"
          sx={{
            mr: 2,
            display: { xs: 'none', md: 'flex' },
            fontFamily: 'Quicksand',
            fontWeight: 700,
            letterSpacing: '.3rem',
            color: 'inherit',
            textDecoration: 'none',
          }}
        >
          OZ
        </Typography>

        <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
          <IconButton
            size="large"
            aria-label="account of current user"
            aria-controls="menu-appbar"
            aria-haspopup="true"
            onClick={handleOpenNavMenu}
            color="inherit"
          >
            <MenuIcon />
          </IconButton>
          <Menu
            id="menu-appbar"
            anchorEl={anchorElNav}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'left',
            }}
            keepMounted
            transformOrigin={{
              vertical: 'top',
              horizontal: 'left',
            }}
            open={Boolean(anchorElNav)}
            onClose={handleCloseNavMenu}
            sx={{
              display: { xs: 'block', md: 'none' },
            }}
          >
            {pages.map(({ name, path }) => (
              <Link key={name} to={path}>
                <MenuItem key={name} onClick={handleCloseNavMenu}>
                  <Typography textAlign="center">{name}</Typography>
                </MenuItem>
              </Link>
            ))}
          </Menu>
        </Box>
        <DataObjIcon sx={{ display: { xs: 'flex', md: 'none' }, mr: 1 }} />
        <Typography
          variant="h5"
          noWrap
          component="a"
          href=""
          sx={{
            mr: 2,
            display: { xs: 'flex', md: 'none' },
            flexGrow: 1,
            fontFamily: 'monospace',
            fontWeight: 700,
            letterSpacing: '.3rem',
            color: 'inherit',
            textDecoration: 'none',
          }}
        >
          OZ
        </Typography>
        <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
          {pages.map(({ name, path }) => (
            <Link key={name} to={path}>
              <Button
                key={name}
                onClick={handleCloseNavMenu}
                sx={{ my: 2, color: 'white', display: 'block' }}
              >
                {name}
              </Button>
            </Link>
          ))}
        </Box>
      </Toolbar>
    </AppBar>
  );
};
export default NavigationBar;
const Triapoze = styled.div`
  height: 0;
  position: absolute;
  right: 0;
`;
